import { Helmet } from "react-helmet"
const Blog = () =>{
    return (<>
        <Helmet>  
           <html lang="en" />  
           <title>Blog page</title>  
           <meta name="description" content="Tutorial for React Helmet" />  
           <meta name="theme-color" content="#E6E6FA" />  
         </Helmet>  
       <h1>This is my Blog page</h1></>)
    
}

export default Blog